
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Confirm from 'vux/src/components/confirm/index.vue'

export default {
name: 'mine',
directives: {
TransferDom
},
components: {
Cell, Group, Flexbox, FlexboxItem,Confirm
},
data () {
return {
cellList: [
{title: '我的直播', value: '开启直播', src: require('../../assets/images/zhibo.png')},
{title: '我的客户', value: '', src: require('../../assets/images/customer.png')},
{title: '购买的课程', value: '', src: require('../../assets/images/buy.png'),name:'purchaseCourse'},
{title: '我的作业', value: '', src: require('../../assets/images/work.png'),name:"myTask"},
{title: '我的收藏', value: '', src: require('../../assets/images/collect.png'),name:"myCollect"},
{title: '关于我们', value: '', src: require('../../assets/images/about.png'),name:"aboutUs"},
{title: '常见问题', value: '', src: require('../../assets/images/question.png'),name:"problem"}
],
money: '8192',
userImg: require('@/assets/images/mine-icon-bg.png'),
userName: '月光稀丶',
confirmStatus: false,
confirmContent: '您尚未购买课程，您尚未购买课程，您尚未购买课程，您尚未购买课程',
}
},
filters: {
RMB: (value) => {
return '￥' + value
}
},
methods: {
goPageWithName (name) {
this.$router.push({
name
})
},
goPage(index,item){
if(item.name){
this.$router.push({
name:item.name
})
}
switch (index) {
case 0:
console.log('000')
this.confirmStatus = true;
break;
case 1:
console.log('111')
this.$vux.toast.text('正在开发ing，敬请期待！','middle')
break;
case 2:
console.log('课程')
break;
case 3:
console.log('333')
break;
case 4:
console.log('444')
break;
case 5:
console.log('555')
break;
case 6:
console.log('666')
break;
default:
console.log('de')
}
},
onHide () {
console.log('on hide')
},
onShow () {
console.log('on show')
},
onCancel () {
console.log('on cancel')
},
onConfirm () {
console.log('on confirm')
this.$router.push({
name:'myLive'
})
}
}
}



